.hire {
    margin: 0;
    text-align: center;
 border-bottom: 1px solid #b46e3c;
 padding-bottom: 20px;

    .title {
        font-family: "Josefin Sans", sans-serif;
        font-size: 15px;
        font-weight: 600;
        color: rgba(255, 255, 255, 0.3);
        letter-spacing: 1px;
        text-transform: uppercase;
        margin: 0 0 25px;
    }

    .hireMe {
        color: #ffffff;
        border: 1px solid #222;
        background: #222;
        letter-spacing: 2px;
        padding: 10px 15px;
        text-transform: uppercase;
        line-height: 17px;
    }
}